.App
{
    text-align: center;
    max-width: 480px;
    margin: 0 auto;
    border-top:8px solid #0275D8;
}

#vaildapp .create-account > *
{
    width: 100%;
}

#vaildapp .general-infomation > .gap,
#vaildapp .payment-method > .gap
{
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction:row;
        flex-direction:row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

#vaildapp .general-infomation > .gap > *,
#vaildapp .payment-method > .gap > *
{
    margin-right: 8px;
    -ms-flex:1 1;
        flex:1 1;
}

#vaildapp .update-profile-picture
{
    border: 2px solid #000000;
    border-radius: 8px;
    padding: 3rem;
    color: #9B9B9B;
}

#vaildapp .update-profile-picture *
{
    margin-right: 1rem;
    display: inline-block;
}

#btn{ margin-top: 1rem; }
.success button, #btn button{ width: 100%; }

.success .fa-smile{ color: #BEE1FF; }

.success .helptext
{
    font-style: italic;
    color: #0275D8;
}


